* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Poppins", system-ui;
}

/* utility classes */
.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*header starts  */
.navbar {
  background-color: #181a1b;
}
.navbar ul {
  list-style: none;
  gap: 2rem;
  font-size: 1.5rem;
}
a {
  text-decoration: none;
  color: white;
  padding: 1rem;
}
li {
  padding: 1rem 2rem;
}

a:hover {
  background: #000;
}
/* header ends */
/* main starts */
.main {
  margin-top: 5rem;
}

.main h1 {
  font-size: 3.5rem;
}

.main img {
  width: 50%;
}
.texts {
  width: 60%;
  margin-top: 1rem;
}

p {
  margin-top: 1rem;
  font-size: 1.25rem;
  font-weight: bold;
}
hr {
  color: black;
  width: 80%;
  margin-top: 3rem;
}
/* main ends  */
/* form starts */
.container {
  width: 100%;
}
.form {
  width: 60%;
  align-items: start;
  font-weight: bold;
  margin-top: 2rem;
  justify-content: flex-start;
}

form {
  display: flex;
  align-items: start;
  flex-direction: column;
  width: 100%;
  gap: 2rem;
}

input {
  font-weight: bold;
  /* margin-top: 1rem; */
  width: 100%;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  padding-left: 1rem;
  /* border-color: #e1e1eb; */
  border-radius: 0.25rem;
}

input::placeholder {
  font-size: 1.5rem;
  color: #e1e1eb;
}

button {
  font-size: 1.5rem;
  background-color: #6455f5;
  color: white;
  padding: 0.25rem 1rem;
  border: none;
  border-radius: 0.25rem;
  margin-bottom: 12rem;
  cursor: pointer;
}
/* form ends */

/* footer starts */

@media screen and (max-width: 900px) {
  .navbar ul {
    font-size: 1.5rem;
    gap: 0rem;
  }
  li {
    padding: 0.5rem 0rem;
 
  }
}

@media screen and (max-width: 470px) {
  .navbar ul {
    font-size: 1.1rem;
  }
}
@media screen and (max-width: 400px) {
  .navbar ul {
    font-size: 0.95rem;
  }

}
